import { Console } from "console";
import * as React from "react";
import { useState } from "react";
import styled from "styled-components";
import axios from "axios";
import { useDispatch } from "react-redux";
import * as config from "../config";
import { useNavigate } from "react-router";
import { loginUser } from "../modules/auth";
import Spinner from "./Spinner";
import Input from "./input/Input";
import Button from "./button/Button";
import { Stack } from "@mui/material";

const BASE_URL = config.API_BASE_URL as string;

export interface LoginProps {}

const LoginWrapper = styled.form`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0.1rem;
`;

export default function Login(props: LoginProps) {
  const [id, setId] = useState("");
  const [pw, setPw] = useState("");
  const [loading, setLoading] = useState(false);
  const [authAlert, setAuthAlert] = useState(false);

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const onSubmitHandler = (e: any) => {
    e.preventDefault();
    setLoading(true);
    const data = { USER_ID: id, PASSWD: pw };
    const config = {
      withCredentials: true,
    };

    axios
      .post(BASE_URL + "/auth/token", data, config)
      .then((res) => {
        setLoading(false);
        if (res.status == 200) {
          dispatch(loginUser());
          navigate(-1);
        } else {
          setAuthAlert(true);
        }
      })
      .catch(() => {
        setAuthAlert(true);
        setLoading(false);
      });
  };

  const onIdHandler = (e: any) => {
    setId(e.target.value);
  };

  const onPwHandler = (e: any) => {
    setPw(e.target.value);
  };

  const onCookieCheckHandler = (e: any) => {
    const config = {
      withCredentials: true,
    };
    axios.get(BASE_URL + "/auth/token", config).then((res) => {
      console.log(res);
      console.log(res.data);
    });
  };

  return (
    <>
      {loading ? (
        <Spinner />
      ) : (
        <>
          <LoginWrapper onSubmit={onSubmitHandler}>
            <Stack direction="column" spacing="0.5rem">
              <Input
                tabIndex={1}
                width={26}
                placeholder="학번을 입력하세요."
                onChange={onIdHandler}
              />
              <Input
                tabIndex={2}
                width={26}
                type="password"
                placeholder="비밀번호를 입력하세요."
                onChange={onPwHandler}
              />
              <Button type="submit" tabIndex={3} width={26}>
                로그인
              </Button>
              <Button
                type="button"
                tabIndex={4}
                onClick={onCookieCheckHandler}
                width={26}>
                쿠키
              </Button>
            </Stack>
          </LoginWrapper>
          {authAlert && (
            <React.Fragment>
              <br />
              <div>
                <a href="https://www.hongik.ac.kr/login.do"> 여기 </a> 에서
                로그인을 해주시고 다시 시도해주세요.
              </div>
            </React.Fragment>
          )}
        </>
      )}
    </>
  );
}
